export type UrlProtocol = "http" | "https" | undefined;

export class UrlHelper {
    public static IsDirectUrl(pathOrUrl: string) {
        return pathOrUrl.startsWith("http:") || pathOrUrl.startsWith("https:") || pathOrUrl.startsWith("blob:");
    }
    public static GetUrlProtocol(url: string): "http" | "https" | undefined {
        const urlLower = url.toLowerCase();
        if (url.startsWith("http:")) {
            return "http";
        }
        if (url.startsWith("https:")) {
            return "https";
        }
        return undefined;
    }
    public static DetachProtocol(url: string): { protocol?: UrlProtocol, pureUrl: string } {
        let protocol = UrlHelper.GetUrlProtocol(url);
        if (!protocol) { return { pureUrl: url }; }

        return { protocol, pureUrl: url.substring(protocol.length + "://".length) };
    }
}